import React from "react";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Chip from "@mui/material/Chip";
import "./RightModal.css";
import { get, isEmpty } from "lodash";
import moment from "moment";
import { Drawer } from "@mui/material";


const ServiceplanDetail = (props) => {
  const { open, onClose, dialogData } = props;
let check;

if(dialogData?.rowData?.phoneConsultation == true) {
    check = 'Phone Consultation'
}else if(dialogData?.rowData?.onlineConsultation == true){
    check = 'Online Consultation'
}else if( dialogData?.rowData?.offlineConsultation == true){
   check = 'Offline Consultation'
}

 
  let filterString = get(dialogData, "rowData.details", "");
  let strippedString = filterString?.replace(/<[^>]+>/g, "");

 
return (
  <>
    <Drawer
      anchor="right"
      open={open}
      onClose={onClose}
      className="plan-details-design"
    >
      <div>
        <Typography variant="h6">Plan Details</Typography>
      </div>

      <div className="plan_details drawer-content">
        <Grid container spacing={2}>
          <Grid item xs={12} sm={12}>
            <Grid container spacing={2}>
              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  Plan Name
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Typography variant="body1">
                  {get(dialogData, "rowData.planName", "")}
                </Typography>
              </Grid>

              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  consultation sessions
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Box>
                  <Chip
                    label={get(dialogData, "rowData.sessionName", "")}
                    sx={{
                      backgroundColor: "secondary.lighter",
                      color: "secondary.dark",
                    }}
                  />
                </Box>
              </Grid>

              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  Duration
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Typography variant="body1">
                  {get(dialogData, "rowData.timeUnit", "")} Min
                </Typography>
              </Grid>

              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  Post Consultation plan (included)
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Chip
                  label={
                    get(dialogData, "rowData.dietPlan", false) ? "Yes" : "No"
                  }
                  sx={{
                    border: "1px solid",
                    backgroundColor: "transparent",
                    borderColor: "secondary.main",
                    color: "secondary.main",
                  }}
                />
              </Grid>

              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  Service Charges
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Typography variant="body1">
                  {get(dialogData, "rowData.amount", "")}{" "}
                  {get(dialogData, "rowData.currencyId.currencySymbol", "")}
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  Number of Consultant
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Typography variant="body1">
                  {get(dialogData, "rowData.numberOfConsultant", "")}
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  Details
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Typography variant="body1">
                  {strippedString && strippedString}
                  {/* {get(dialogData, "rowData.details", "")} */}
                </Typography>
              </Grid>

              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  Consultation Type
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Typography variant="body1">{check && check}</Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  Time Unit
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Typography variant="body1">
                  {get(dialogData, "rowData.timeUnit", "")}
                </Typography>
              </Grid>

              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  Created Date
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  {isEmpty(get(dialogData, "rowData.createDate", ""))
                    ? "Date Not Found"
                    : moment(dialogData.rowData.createDate).format("LL")}
                </Typography>
              </Grid>

              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  Last Date
                </Typography>
              </Grid>
              <Grid item xs={6} md={6}>
                <Typography variant="subtitle1" color="text.primary">
                  {isEmpty(get(dialogData, "rowData.updateDate", ""))
                    ? "Not Updated"
                    : moment(dialogData.rowData.updateDate).format("LL")}
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </div>
    </Drawer>
  </>
);
};

export default ServiceplanDetail;
